<template>
  <div class="page-card-content" v-loading="loading">
    <el-table
      :data="tableData"
      stripe
      row-key="id"
      :header-cell-style="{ background: '#f7f8fb' }"
      height="650"
      @select="handlSelect"
      @select-all="handlSelect"
    >
      <template slot="empty">
        <div class="empty-img">
          <img src="@/assets/img/empty.png" />
          <p>暂无数据</p>
        </div>
      </template>
      <el-table-column type="selection" width="55"> </el-table-column>
      <el-table-column label="key" prop="key"> </el-table-column>
      <el-table-column label="token时效" prop="value"> </el-table-column>
      <el-table-column label="描述" prop="desc"> </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" @click="editorClick(scope.row)">编辑</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-pagination
      class="main-pagination"
      :total="pageInfo.total"
      :current-page="searchForm.pageNum"
      :page-size="searchForm.pageSize"
      :page-sizes="pageInfo.pageSizes"
      @current-change="handlePageChanged"
      @size-change="handlePageSizeChanged"
      background
      layout="total, sizes, prev, pager, next"
    />
    <!-- 新增-->
    <el-dialog
      title="编辑"
      width="650px"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      label-width="120px"
      style="margin-top:-20px"
    >
      <el-form ref="addForm" :model="addForm" label-width="100px" :rules="formRules">
        <el-form-item label="key" prop="name">
          <el-input v-model="addForm.key" class="labelWidth" disabled></el-input>
        </el-form-item>
        <el-form-item label="token时效" prop="value">
          <el-input type="number" v-model="addForm.value" class="labelWidth"></el-input>
        </el-form-item>
        <el-form-item label="描述">
          <el-input v-model="addForm.desc" class="labelWidth" maxlength="150"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm" style="float: right;">
            保存
          </el-button>
          <el-button
            type="primary"
            @click="submitForm(1)"
            plain
            style="float: right;margin-right:15px"
          >
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from '@/api/index.js'
export default {
  filters: {
    filterRunningTime(val) {
      if (val) {
        var hours = parseInt(val / (60 * 60))
        var minutes = parseInt((val % (60 * 60)) / 60)
        var seconds = parseInt(val % 60)
        if (minutes < 10) {
          minutes = '0' + minutes
        }
        if (seconds < 10) {
          seconds = '0' + seconds
        }
        return hours + ':' + minutes + ':' + seconds
      } else {
        return '--'
      }
    }
  },
  data() {
    return {
      loading: false,
      tableData: [],
      pageInfo: {
        pageSizes: [10, 20, 50],
        total: 0
      },
      searchForm: {
        pageNum: 1,
        pageSize: 10
      },
      addForm: {
        key: '',
        value: 0,
        desc: ''
      },
      dialogVisible: false
    }
  },
  created() {
    this.getTableData()
  },
  mounted() {},
  methods: {
    editorClick(row) {
      this.dialogVisible = true
      this.addForm = row
    },
    submitForm(type) {
      if (type == 1) {
        this.dialogVisible = false
      }
      this.addForm.id = parseInt(this.addForm.id)
      API.getSystemEditor(this.addForm).then(res => {
        if (res.status == 200) {
          this.dialogVisible = false
          this.$message.success(res.message)
        } else {
          this.$message.error(res.message)
        }
      })
    },
    handlSelect(row) {
      this.clickRow = row
    },

    getTableData(init) {
      this.loading = true
      if (init) {
        this.searchForm.pageNum = 1
        this.searchForm.pageSize = 10
      }
      const form = JSON.parse(JSON.stringify(this.searchForm))
      API.getSystem(form)
        .then(res => {
          if (res.status == 200) {
            this.pageInfo.total = res.data.total
            this.tableData = res.data.records
          } else {
            this.$message.error(res.message)
          }
        })
        .finally(() => {
          this.loading = false
        })
    },
    handlePageChanged(value) {
      this.searchForm.pageNum = value
      this.getTableData(false)
    },
    handlePageSizeChanged(value) {
      this.searchForm.pageSize = value
      this.getTableData(false)
    }
  }
}
</script>

<style lang="scss" scoped>
::v-deep .el-table-column--selection .cell {
  padding-left: 10px;
}
#code {
  height: 500px;
  width: 500px;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 10px;
  border: none;
}
.detailData {
  margin-bottom: 40px;
  div {
    line-height: 30px;
  }
}
::v-deep .el-upload-list {
  max-height: 400px;
  overflow: auto;
}
.mr50 {
  margin-right: 60px;
  width: 250px;
}
::v-deep .el-upload-dragger {
  width: 96px;
  height: 96px;
}
::v-deep .el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
::v-deep .el-upload--picture-card {
  width: 96px;
  height: 96px;
  line-height: 96px;
}

.labelWidth {
  width: 400px !important;
}
</style>
